
.login {
    background-color: white; 
    /* rgb(120, 0, 150) */
    /* border-radius: 15px; */
    height: 100vh;
    padding-top: 120px;
    padding-bottom: 70px;
}

.login-logo{
    /* padding-top: 30px; */
    display: flex;
    align-items: center;
    flex-direction: column;
}

.login-logo > .login-logo-image {
    height: 300px;
    color: black;
    background-color: rgb(250, 70, 50);
    border-radius: 50%;
    padding: 20px;
}
.login > h1 {
    text-align: center;
    color: aqua;
    font-size: 3em;
}

form > .login-form {
    display: flex;
    flex-direction: column;
    margin: 20px 50px;
}

.login > form {
    display: flex;
    flex-direction: column;
}

.login-form > input {
    margin-top: 20px;
    padding-left: 30px;
    padding-bottom: 8px;
    font-size: 2em;
}

