.login-form-header > button:hover {
    color: black;
    background-color: lightgray;
}

h3 {
    height: auto;
}

h3 ,h2{
    margin-top: 20px;
    font-size: 1.5em;
    text-align: center;
}

h3 > span {
    color:  red;
    cursor: pointer;
}

h3 > span:hover {
    color:  white;
}

.login > h1 {
    margin-top: 20px;
}

.login-error{
    color: red;
}

.login-get-start {
    border: none;
    cursor: pointer;
    border-radius: 15px;
    background-color: rgb(250, 70, 50);
}
.login-get-start-div{
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-left: 50px;
    margin-right: 50px;
    /* font-size: 2em; */
}
.get-start{
    font-size: 2.5em;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
}

.login-get-start:hover{
    background-color: black;
}

